@use 'abstracts/mixins' as *;

.jumbotron {
  padding-top: 134px;

  @include md {
    padding-top: 76px;
  }

  .section-title {
    color: var(--white);
    font-size: 20px;
    margin-bottom: 24px;
    padding-left: 0;
    margin-left: -1px;
    letter-spacing: 2px;

    &::before {
      font-size: 20px;
      margin-right: 4px;

      @include md {
        font-size: 16px;
      }
    }

    @include md {
      font-size: 16px;
      margin-bottom: 22px;
      letter-spacing: 1.6px;
    }
  }

  &__subtitle {
    text-transform: uppercase;
    font-size: 30px;
    letter-spacing: 1.5px;
    line-height: 46px;
    color: var(--white);
    font-weight: 300;
    margin-bottom: 58px;
    max-width: 940px;
    margin-left: -2px;

    @include md {
      font-size: 20px;
      letter-spacing: 1px;
      line-height: 28px;
      padding-left: 2px;
      margin-bottom: 112px;
    }

    span {
      color: var(--gold);
    }
  }

  &__description {
    display: flex;
    justify-content: flex-end;
    padding-bottom: 50px;
    padding-right: 154px;

    @include md {
      padding-right: 9px;
      padding-bottom: 46px;
    }

    p {
      position: relative;
      max-width: 665px;
      width: 100%;
      color: var(--white);
      font-size: 24px;
      line-height: 30px;
      letter-spacing: 1.2px;
      font-weight: 300;
      font-family: var(--unnamed-font-family-garamond-premier-pro);

      @include md {
        font-size: 16px;
        line-height: 22px;
        letter-spacing: 0.8px;
        padding-left: 63px;
        padding-right: 5px;
      }

      &::before {
        content: '';
        display: block;
        background-color: var(--gold);
        width: 2px;
        margin-top: 2px;
        height: 770px;
        position: absolute;
        top: 0;
        left: -60px;

        @include md {
          height: 616px;
          left: 39px;
          display: none;
        }
      }
    }
  }

  &__parallax {
    background: url('./../assets/how-it-works-parallax-bg.webp');
    min-height: 400px;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    @include md {
      min-height: 340px;
      background-attachment: scroll !important;
      background-position: center !important;
    }
  }
}

.intro {
  padding-top: 100px;

  @include md {
    padding-top: 44px;
  }

  .container {
    display: flex;
    align-items: flex-end;
    flex-direction: column;
  }

  &__items {
    display: flex;
    flex-wrap: wrap;
    flex-shrink: 0;
    max-width: 819px;
    padding-right: 12px;
    padding-left: 3px;
    width: 100%;
  }

  &__item {
    margin-bottom: 65px;
    max-width: 135px;
    margin-right: 30px;
    text-align: center;

    @include sm {
      max-width: 100%;
      width: 100%;
      margin-right: 0;
    }

    &:last-of-type {
      margin-right: 0;
    }

    &__icon {
      margin-bottom: 28px;
    }

    &__value {
      color: var(--green-light);
      text-align: center;
      font-size: 26px;
      letter-spacing: 0.65px;
      margin-bottom: 11px;
    }

    &__label {
      color: var(--dark-green);
      text-align: center;
      font-size: 16px;
      line-height: 22px;
      letter-spacing: 0.8px;
      font-weight: 300;
      font-family: var(--unnamed-font-family-garamond-premier-pro);
    }
  }

  &__text {
    flex-shrink: 0;
    margin-right: 179px;
    max-width: 640px;
    padding-right: 12px;
    padding-left: 3px;
    width: 100%;
    color: var(--dark-grey);
    font-size: 20px;
    line-height: 28px;
    letter-spacing: 0.5px;
    font-weight: 300;
    font-family: var(--unnamed-font-family-garamond-premier-pro);
    margin-bottom: 28px;

    @include md {
      font-size: 16px;
      line-height: 22px;
      letter-spacing: 0.4px;
      width: 100%;
      padding-left: 63px;
      margin-right: 0;
      padding-right: 5px;
      margin-bottom: 22px;
    }
  }
}

.in-motion {
  padding-top: 46px;

  .desktop-container {
    padding: 0 30px;
    width: 100%;
    max-width: 1190px;
    margin: 0 auto;
    @include md {
      padding: 0;
    }
  }

  &__video {
    position: relative;
    overflow: hidden;
    width: 100%;
    padding-top: 56.25%;
    margin-top: 24px;

    iframe {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      width: 100%;
      height: 100%;
    }
  }

  &__text {
    display: flex;
    align-items: flex-end;
    flex-direction: column;
    padding-top: 47px;
    padding-right: 111px;

    @include md {
      padding-top: 31px;
      padding-right: 0;
    }

    p {
      color: var(--dark-grey);
      font-size: 20px;
      line-height: 28px;
      letter-spacing: 0.5px;
      font-weight: 300;
      font-family: var(--unnamed-font-family-garamond-premier-pro);
      max-width: 640px;
      width: 100%;
      padding-right: 20px;
      margin-bottom: 20px;

      @include md {
        font-size: 16px;
        line-height: 22px;
        letter-spacing: 0.4px;
        padding-left: 63px;
        padding-right: 5px;
      }
    }
  }

  &__features {
    display: flex;
    justify-content: flex-end;
    padding-top: 47px;
    padding-bottom: 76px;
    padding-right: 111px;

    &__container {
      max-width: 640px;
      width: 100%;
      display: flex;
      flex-wrap: wrap;
    }

    @include md {
      padding-top: 31px;
      padding-bottom: 101px;
      padding-right: 0;
    }
  }

  &__feature {
    width: 33%;
    text-align: center;
    margin-bottom: 35px;

    @include md {
      width: 100%;
    }

    img {
      margin-bottom: 22px;
    }

    h5 {
      color: var(--mid-green);
      font-size: 16px;
      line-height: 26px;
      letter-spacing: 0.8px;
      text-transform: uppercase;
      font-weight: 500;
    }
  }
}

.services {
  padding-bottom: 60px;

  .how-it-works__title {
    margin-bottom: 14px;

    @include md {
      margin-bottom: 40px;
    }
  }

  &__item {
    background: var(--light-grey);
    padding: 38px 218px 54px 68px;
    margin-bottom: 18px;

    @include md {
      padding: 37px 20px;
    }

    &__title {
      color: var(--mid-green);
      font-size: 22px;
      letter-spacing: 2.2px;
      margin-bottom: 19px;
      text-transform: uppercase;

      @include md {
        font-size: 16px;
        letter-spacing: 1.6px;
        margin-bottom: 16px;
      }
    }

    &__list {
      padding-left: 33px;
      margin-bottom: 39px;

      @include md {
        padding-left: 0;
        margin-bottom: 0;
      }

      &__item {
        font-family: var(--unnamed-font-family-garamond-premier-pro);
        font-weight: 300;
        font-size: 20px;
        line-height: 28px;
        letter-spacing: 0.5px;
        color: var(--dark-grey);
        display: flex;

        @include md {
          font-size: 16px;
          line-height: 22px;
          letter-spacing: 0.4px;
        }

        &::before {
          content: 'â€¢';
          color: var(--amber);
          font-size: 20px;
          padding-right: 20px;
        }
      }
    }

    &__button-container {
      margin-left: 32px;
      max-width: 340px;

      @include md {
        margin-left: 0;
        margin-top: 20px;
      }

      .button-outlined {
        padding-left: 22px;
        @include md {
          padding-left: 0;
          font-size: 10px;
        }
      }
    }
  }
}

.benefits {
  background: url('./../assets/benefits-bg.png') no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 88px;
  padding-bottom: 42px;

  @include md {
    padding-top: 43px;
  }

  &__top {
    text-align: center;
  }

  &__logo {
    margin-bottom: 49px;

    @include md {
      margin-bottom: 42px;
    }
  }

  &__title {
    text-transform: uppercase;
    font-size: 28px;
    letter-spacing: 2.8px;
    color: var(--white);
    margin-bottom: 26px;

    @include md {
      font-size: 24px;
      letter-spacing: 2.4px;
      margin-bottom: 20px;
    }
  }

  &__description {
    font-family: var(--unnamed-font-family-garamond-premier-pro);
    font-weight: 300;
    font-size: 24px;
    line-height: 30px;
    letter-spacing: 1.2px;
    color: var(--white);
    max-width: 850px;
    margin: 0 auto 60px auto;

    @include md {
      font-size: 16px;
      line-height: 22px;
      letter-spacing: 0.8px;
      padding: 0 25px;
      margin-bottom: 34px;
    }
  }

  &__items {
    display: flex;
    flex-wrap: wrap;
    padding: 0 61px 0 59px;
    justify-content: space-between;

    @include md {
      padding: 0;
    }
  }

  &__item {
    margin-bottom: 65px;
    max-width: 440px;
    width: 44%;

    @include md {
      padding-right: 10px;
      margin-bottom: 26px;
    }

    &__title {
      color: var(--white);
      text-transform: uppercase;
      letter-spacing: 2.2px;
      font-size: 22px;
      margin-bottom: 17px;

      @include md {
        font-size: 16px;
        letter-spacing: 1.6px;
        margin-bottom: 14px;
      }
    }

    &__text {
      font-family: var(--unnamed-font-family-garamond-premier-pro);
      font-weight: 300;
      font-size: 20px;
      line-height: 28px;
      letter-spacing: 0.5px;
      color: var(--white);

      @include md {
        font-size: 16px;
        letter-spacing: 0.8px;
        line-height: 22px;
      }
    }
  }
}

.summary {
  background: var(--white);

  &__logo {
    display: flex;
    justify-content: center;
    padding: 69px 0 23px 0;

    @include md {
      padding-top: 53px;
    }

    svg,
    path,
    g {
      fill: var(--mid-green);
    }
  }

  &__title {
    font-size: 28px;
    text-align: center;
    line-height: 42px;
    text-transform: uppercase;
    letter-spacing: 2.8px;
    color: var(--green-light);
    margin: 0 0 17px 0;

    @include md {
      font-size: 16px;
      line-height: 24px;
      letter-spacing: 1.6px;
      margin-bottom: 13px;
    }
  }

  &__text {
    font-family: var(--unnamed-font-family-garamond-premier-pro);
    font-size: 24px;
    line-height: 30px;
    letter-spacing: 1.2px;
    color: var(--dark-grey);
    margin: 0 auto 37px auto;
    text-align: center;
    max-width: 850px;

    @include md {
      font-size: 16px;
      line-height: 22px;
      margin-bottom: 33px;
    }
  }

  &__items {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    max-width: 850px;
    margin: 0 auto 47px auto;
  }

  &__item {
    width: 33%;
    text-align: center;
    margin-bottom: 30px;

    @include md {
      width: 100%;
    }

    img {
      margin-bottom: 25px;
    }

    h4 {
      color: var(--mid-green);
      text-align: center;
      font-size: 16px;
      line-height: 26px;
      font-weight: 500;
      letter-spacing: 0.8px;
      text-transform: uppercase;
      max-width: 230px;
      margin: 0 auto;
    }
  }

  &__button {
    padding-bottom: 87px;
    max-width: 340px;
    margin: 0 auto;
  }
}

// COMMON

.how-it-works__title {
  text-transform: uppercase;
  font-size: 28px;
  letter-spacing: 2.8px;
  color: var(--mid-green);
  display: flex;
  align-items: center;
  padding-left: 37px;

  @include md {
    font-size: 16px;
    letter-spacing: 1.6px;
    padding-left: 0;
  }

  &::before {
    content: '';
    display: block;
    background: url('./../assets/title-logo.svg');
    background-size: cover;
    margin-right: 16px;
    position: relative;
    top: 5px;
    height: 72px;
    width: 72px;

    @include md {
      content: '';
      width: 41px;
      margin-right: 9px;
      height: 41px;
      top: -1px;
    }
  }
}

.section-container {
  padding-left: 126px;
  max-width: 1066px;

  @include md {
    padding-left: 0;
  }
}

.how-it-works-container {
  background-color: var(--white);
  background-image: url('./../assets/pricing-bg.webp');
  background-position: right -310px top 670px;
  background-size: 876px;
  background-repeat: no-repeat;

  @include md {
    background-size: 517px;
    background-position: right -270px top 250px;
  }
}
